import React, { Component } from 'react';

//import Slider from 'react-animated-slider';
//import 'react-animated-slider/build/horizontal.css';
import {
    Card, Button,/*CardImg,*/ CardTitle, CardText, CardDeck,
    CardSubtitle, CardBody
} from 'reactstrap';

import PL from '../Images/pl.svg';
import GB from '../Images/gb.svg';

import { strings, technology } from '../App'

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

import STCOS from '../Images/Survive_The_Clash_Of_Spiders_-_Screenshot_1.jpg';
import STCOS_2 from '../Images/Survive_The_Clash_Of_Spiders_-_Screenshot_2.jpg';
import STCOS_3 from '../Images/Survive_The_Clash_Of_Spiders_-_Screenshot_3.jpg';
import STCOS_4 from '../Images/Survive_The_Clash_Of_Spiders_-_Screenshot_4.jpg';

let imagesArkanoidClone = [
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_1.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_2.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_3.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_4.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_5.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_6.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_7.jpg',
    'https://raw.githubusercontent.com/pawel54321/Arkanoid-Clone/main/Screen_8.jpg'
];

let imagesBoardGame = [
    'https://raw.githubusercontent.com/pawel54321/Board-Game/master/Screen.jpg',
    'https://raw.githubusercontent.com/pawel54321/Board-Game/master/Screen%202.jpg'
];

let imagesTicTacToe = [
    'https://raw.githubusercontent.com/pawel54321/Tic-Tac-Toe/master/Screen.jpg',
    'https://raw.githubusercontent.com/pawel54321/Tic-Tac-Toe/master/Screen%202.jpg'
];

let imagesBounceBall = [
    'https://raw.githubusercontent.com/pawel54321/Bounce-Ball/master/Screen.jpg'
];

let imagesSpeedyGame = [
    'https://raw.githubusercontent.com/pawel54321/Speedy-Game/master/Screen.jpg'
];


const images = [

    imagesArkanoidClone[0],
    imagesArkanoidClone[1],
    imagesArkanoidClone[2],
    imagesArkanoidClone[3],
    imagesArkanoidClone[4],
    imagesArkanoidClone[5],
    imagesArkanoidClone[6],
    imagesArkanoidClone[7],

    STCOS,
    STCOS_2,
    STCOS_3,
    STCOS_4,

    imagesBoardGame[0],
    imagesBoardGame[1],

    imagesBounceBall[0],

    imagesTicTacToe[0],
    imagesTicTacToe[1],

    imagesSpeedyGame[0]

];

class Prototypes extends Component {


    constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false,
        };
    }


    render() {

        const { photoIndex, isOpen } = this.state;

        return (
            <div style={{ float: 'left', paddingBottom: '30px' }}>


                {isOpen && (
                    <Lightbox
                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                        discourageDownloads={false}
                        enableZoom={false}
                        clickOutsideToClose={false}

                    />
                )}

                <h4 style={{ paddingLeft: '30px', paddingTop: '30px' }}>{technology[0]} | {strings.heading_Prototypes}</h4>
                <CardDeck style={{ paddingTop: '30px', float: 'left' }}>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 0, })}>
                                <img src={imagesArkanoidClone[0]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 1, })}>
                                <img src={imagesArkanoidClone[1]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 2, })}>
                                <img src={imagesArkanoidClone[2]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 3, })}>
                                <img src={imagesArkanoidClone[3]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 4, })}>
                                <img src={imagesArkanoidClone[4]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 5, })}>
                                <img src={imagesArkanoidClone[5]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 6, })}>
                                <img src={imagesArkanoidClone[6]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 7, })}>
                                <img src={imagesArkanoidClone[7]} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <CardTitle>'Arkanoid Clone'</CardTitle>
                            <CardSubtitle><i class="fa fa-desktop"></i> Desktop | <i class="fa fa-comments"></i> Multiplayer (Hot Seat) | <img src={GB} alt="gb" height="20" width="20"></img> {strings.english_version}</CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Arkanoid-Clone/tree/main/Build"><i class="fa fa-play-circle"></i> {strings.button_github}</Button> <Button color="info" href="https://github.com/pawel54321/Arkanoid-Clone"><i class="fa fa-info-circle"></i> {strings.button_github_2}</Button><br /><br />
                            <Button color="danger" href="https://github.com/pawel54321/Arkanoid-Clone/tree/main/Project"><i class="fa fa-code"></i> {strings.button_github_3}</Button>
                        </CardBody>
                    </Card>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                </CardDeck>
                <CardDeck style={{ paddingTop: '30px', float: 'left' }}>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 8, })}>
                                <img src={STCOS} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 9, })}>
                                <img src={STCOS_2} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 10, })}>
                                <img src={STCOS_3} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 11, })}>
                                <img src={STCOS_4} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <CardTitle>'Survive The Clash Of Spiders'</CardTitle>
                            <CardSubtitle><i class="fa fa-android"></i><i class="fa fa-cardboard"></i> Oculus/Cardboard (VR) | <i class="fa fa-user"></i> Singleplayer | <img src={GB} alt="gb" height="20" width="20"></img> {strings.english_version}<br /><br />
                                <i>{strings.spiders_1}<br />
                                    {strings.spiders_2}<br />
                                    <b>{strings.spiders_3}</b></i></CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Survive-The-Clash-Of-Spiders"><i class="fa fa-play-circle"></i> {strings.button_github}</Button> <Button color="info" disabled href=""><i class="fa fa-info-circle"></i> {strings.button_github_2}</Button>
                        </CardBody>
                    </Card>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                </CardDeck>
                <CardDeck style={{ paddingTop: '30px', float: 'left' }}>
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 12, })}>
                                <img src={imagesBoardGame[0]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 13, })}>
                                <img src={imagesBoardGame[1]} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <CardTitle>'Board Game'</CardTitle>
                            <CardSubtitle><i class="fa fa-desktop"></i> Desktop | <i class="fa fa-comments"></i> Multiplayer (Hot Seat) | <img src={PL} alt="pl" height="20" width="20"></img> {strings.polish_version}</CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Board-Game"><i class="fa fa-play-circle"></i> {strings.button_github}</Button>
                        </CardBody>
                    </Card> 
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 14, })}>
                                <img src={imagesBounceBall[0]} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <CardTitle>'Bounce Ball'</CardTitle>
                            <CardSubtitle><i class="fa fa-desktop"></i> Desktop | <i class="fa fa-user"></i> Singleplayer</CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Bounce-Ball"><i class="fa fa-play-circle"></i> {strings.button_github}</Button>
                        </CardBody>
                    </Card>
                </CardDeck>
                <CardDeck style={{ paddingTop: '30px', float: 'left' }}>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 15, })}>
                                <img src={imagesTicTacToe[0]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 16, })}>
                                <img src={imagesTicTacToe[1]} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <CardTitle>'Tic Tac Toe'</CardTitle>
                            <CardSubtitle><i class="fa fa-desktop"></i> Desktop | <i class="fa fa-comments"></i> Multiplayer (Hot Seat) | <img src={GB} alt="gb" height="20" width="20"></img> {strings.english_version}<br /><br />
                                <i><b>{strings.TicTacToe_1}</b></i>
                            </CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Tic-Tac-Toe"><i class="fa fa-play-circle"></i> {strings.button_github}</Button>
                        </CardBody>
                    </Card>
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 17, })}>
                                <img src={imagesSpeedyGame[0]} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <br />
                            <CardTitle>'Speedy Game'</CardTitle>
                            <CardSubtitle><i class="fa fa-desktop"></i> Desktop | <i class="fa fa-user"></i> Singleplayer | <img src={PL} alt="pl" height="20" width="20"></img> {strings.polish_version}</CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Speedy-Game"><i class="fa fa-play-circle"></i> {strings.button_github}</Button>
                        </CardBody>
                    </Card>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                </CardDeck>
            </div>
        );
    }
}


export default Prototypes;
