import React, { Component } from 'react';

//import Slider from 'react-animated-slider';
//import 'react-animated-slider/build/horizontal.css';
import {
    Card, Button,/*CardImg,*/ CardTitle, CardText, CardDeck,
    CardSubtitle, CardBody
} from 'reactstrap';

//import PL from '../Images/pl.svg';
import GB from '../Images/gb.svg';

import { strings, technology } from '../App'

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css'; 

let imagesSpaceshipShooter2D = [
    'https://raw.githubusercontent.com/pawel54321/Spaceship-Shooter-2D/main/Screen_1.jpg',
    'https://raw.githubusercontent.com/pawel54321/Spaceship-Shooter-2D/main/Screen_2.jpg',
    'https://raw.githubusercontent.com/pawel54321/Spaceship-Shooter-2D/main/Screen_3.jpg',
    'https://raw.githubusercontent.com/pawel54321/Spaceship-Shooter-2D/main/Screen_4.jpg',
    'https://raw.githubusercontent.com/pawel54321/Spaceship-Shooter-2D/main/Screen_5.jpg' 
];

const images = [
    imagesSpaceshipShooter2D[0],
    imagesSpaceshipShooter2D[1],
    imagesSpaceshipShooter2D[2],
    imagesSpaceshipShooter2D[3],
    imagesSpaceshipShooter2D[4]
];

class GroupApps extends Component {


    constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false,
        };
    }


    render() {

        const { photoIndex, isOpen } = this.state;

        return (
            <div style={{ float: 'left', paddingBottom: '30px' }}>


                {isOpen && (
                    <Lightbox
                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                        discourageDownloads={false}
                        enableZoom={false}
                        clickOutsideToClose={false}
                  
                    />
                )}

                <h4 style={{ paddingLeft: '30px', paddingTop: '30px' }}>{technology[1]} | {strings.heading_GroupApps}</h4>

                <CardDeck style={{ paddingTop: '30px', float: 'left' }}>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                    <Card className="Card">
                        <Carousel showThumbs={false} stopOnHover={true} emulateTouch={false} infiniteLoop={true} autoPlay={true} transitionTime={1500} interval={3000}>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 0, })}>
                                <img src={imagesSpaceshipShooter2D[0]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 1, })}>
                                <img src={imagesSpaceshipShooter2D[1]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 2, })}>
                                <img src={imagesSpaceshipShooter2D[2]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 3, })}>
                                <img src={imagesSpaceshipShooter2D[3]} alt="" />
                            </div>
                            <div onClick={() => this.setState({ isOpen: true, photoIndex: 4, })}>
                                <img src={imagesSpaceshipShooter2D[4]} alt="" />
                            </div>
                        </Carousel>
                        <CardBody>
                            <CardTitle>'Spaceship Shooter 2D'</CardTitle>
                            <CardSubtitle><i class="fa fa-desktop"></i> Desktop | <i class="fa fa-comments"></i> Multiplayer (Hot Seat) | <img src={GB} alt="gb" height="20" width="20"></img> {strings.english_version}</CardSubtitle>
                            <CardText></CardText>
                            <Button color="success" href="https://github.com/pawel54321/Spaceship-Shooter-2D/tree/main/Build"><i class="fa fa-play-circle"></i> {strings.button_github}</Button> <Button color="info" href="https://github.com/pawel54321/Spaceship-Shooter-2D"><i class="fa fa-info-circle"></i> {strings.button_github_2}</Button>
                        </CardBody>
                    </Card>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                    <Card className="Card" style={{ borderStyle: "none" }}>
                    </Card>
                </CardDeck>
            </div>
            );
    }
}


export default GroupApps;
